.image-icon-list ul li {
  margin: 3px 3px 3px 0;
  height: 100px;
  padding: 1px;
  font-size: 1em;
  position: relative;
  display: block;
}
.image-icon-list ul li a {
  position: absolute;
}
.image-icon-list ul li a.img-checkbox {
  display: block;
  top: 4px;
  width: 16px;
  height: 16px;
}
.image-icon-list ul li a.img-checkbox.enabled {
  background: url('/assets/images/checkbox_full.png');
}
.image-icon-list ul li a.img-checkbox.disabled {
  background: url('/assets/images/checkbox_empty.png');
}
.image-icon-list ul li a.edit {
  left: 25px;
}
.image-icon-list ul li a.zoom {
  left: 40px;
  top: 40px;
}
.image-icon-list ul li a.zoom img {
  width: 32px;
  height: 32px;
}
.image-icon-list ul li a.act {
  left: 5px;
}
.image-icon-list ul li a.delete {
  left: 80px;
}
.image-icon-list ul li.disabled a.act-checkbox {
  background: url('/assets/images/checkbox_empty.png');
}
.image-icon-list ul li .image {
  width: 100px;
  height: 100px;
  position: absolute;
}
.image-icon-list.image-icon li {
  float: left;
  width: 100px;
}
.image-icon-list.image-icon .full {
  display: none;
}
.image-icon-list.image-full li {
  width: 100%;
}
.image-icon-list.image-full .full {
  margin-left: 110px;
}

